/**
 * ----------------------------------------------
 * Font face
 * 
 */

$body-font: 'Lato-Regular',sans-serif;

$heading-font: 'GothamProRegular', sans-serif;
$opan-sans-font: 'Open Sans',sans-serif;
$slab-heading-font: 'Roboto Slab', sans-serif;
$barlow-heading-font: 'Barlow', sans-serif;
$fontawesome-font-family: "FontAwesome";
$lato-bold: "Lato-Bold",sans-serif;
$lato-black: "Lato-Black",sans-serif;
$gothem-bold: "GothamProBold",sans-serif;

/**
 * ----------------------------------------------
 * Font Sizes
 * ----------------------------------------------
 *
*/
$default-font-size: 18px;
$grid-title-font-size: 14px;
$author-size: 12px;
$service-icon-font-size: 2.75rem;


/**
 * ----------------------------------------------
 * COLOR CODES
 * ----------------------------------------------
 *
*/

$white:                 #ffffff;
$black:                 #000000;
$default-color:         #777;
$heading-color:         #343A3A;
$btn-default-color:     #ddd;
$subtitle-color:        #888888;
$border-color:          #f1f2f3;
$bg-light:              #fcfcfc !important;
$light:                 #fcfcfc;
$nav-color:             rgba(0,0,0,1);
$para-color:            #464646;
$tab-title-color:       rgba(0,0,0,0.5);
$overlay-bg:            rgba(0,0,0,1);
$rating-color:          #fac50f;
$image-path:            '../../images';
$footer-bg:             #444444;
$copyright-bg:          #2E303C;
$link-color:            #222;
$dot-color:             #ccc;
$sidemenu-bg:           #0c0c0c; 
$js-blue:               #364ba0;
/**
* ----------------------------------------------
* Shadows CODES
* ----------------------------------------------
*
*/
$box-shadow: 0 0 60px 10px rgba(220,220,220,.3);



/**
